import React from "react";
import {Header} from "../block/Header";
import {SideMenuLeft} from "../../Components/Admin/SideMenuLeft";
import {OutputMain} from "./OutputMain";



class OutputPage extends React.Component {
    render() {
        return (
            <>
                <Header pageName={'Output'}/>
                <div className="main">
                    <SideMenuLeft nowIs={'output'}/>
                    <OutputMain/>
                </div>
            </>
        );
    }
}

export default OutputPage