import React from "react";
import {JackpotStatistics} from "./UI/JackpotStatistics";
import {CoinFlipStatistics} from "./UI/CoinFlipStatistics";


function GamesMain () {
    return(
        <div className="base">
            <div className="base__wrapper base__wrapper--games">
                <div className="nano base__games">
                    <div className="nano-content">
                        <div className="games__wrap">
                            <JackpotStatistics/>
                            <CoinFlipStatistics/>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export {
    GamesMain
}