import React from "react";
import {WalletTable} from "./UI/WalletTable";


function WalletMain () {
    return(
        <div className="base">
            <div className="base__wrapper base__wrapper--promo">
                <div className="table-base__checker checker">
                    <p className="checker__text">Выбрано <span className="check-counter">10</span> записей</p>
                    <a className="checker__all">Выбрать все записи<span></span></a>
                    <a className="checker__cancel">Отменить выделение<span></span></a>
                </div>
                <form action="{{ route(Route::currentRouteName())  }}" style={{display:"none"}} className="sorted-form">
                    <input type="text" name="sort"/>
                </form>
                <WalletTable/>
                <div className="base__bottom">
                    {/*Start paginations */}
                    <div className="paginations">
                        {/*{{$users->appends($arraySort)->links()}}*/}
                        <p className="paginations__info">Показано
                            от 22 до 33 из 300 записей
                            {/*{{($users->currentPage() * $paginate) - $paginate + 1}} */}
                            {/*до*/}
                            {/*{{$users->total() < $paginate ? $paginate :  $users->total()}} */}
                            {/*из*/}
                            {/*{{$users->total()}} */}
                            {/*записей*/}
                        </p>
                    </div>
                    {/*End paginations*/}
                    <button className="base__save" onClick="MainFunction.saveUser();">Сохранить</button>
                </div>
            </div>
        </div>
    )
};

export {
    WalletMain
}