import React from "react";
import JackpotPage from "./Pages/JackpotPage/JackpotPage";
import CoinFlipPage from "./Pages/CoinflipPage/CoinflipPage";
import KingOfTheHillPage from "./Pages/KingOfTheHillPage/KingOfTheHillPage";
import CrashPage from "./Pages/CrashPage/CrashPage";
import HelpPage from "./Pages/HelpPage/HelpPage";
import ProfilePage from "./Pages/ProfilePage/ProfilePage";
import DailyBonusPage from "./Pages/DailyBonusPage/DailyBonusPage";
import {Error404} from "./Pages/404/404";
import { Switch, Route } from 'react-router-dom';

function UserApp() {
    return(
        <Switch>
            <Route exact path='/'>
                <JackpotPage/>
            </Route>
            <Route exact path='/coinflip'>
                <CoinFlipPage/>
            </Route>
            <Route exact path='/king-of-the-hill'>
                <KingOfTheHillPage/>
            </Route>
            <Route exact path='/crash'>
                <CrashPage/>
            </Route>
            <Route exact path='/help'>
                <HelpPage/>
            </Route>
            <Route exact path='/profile'>
                <ProfilePage/>
            </Route>
            <Route exact path='/daily-bonus'>
                <DailyBonusPage/>
            </Route>
            <Route component={Error404}/>
        </Switch>
    )
}
export {
    UserApp
}