import React, {useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {closeModal, openModal} from "../../../Actions/ModalAction";


const BetKingOfTheHill = () => {
    const balance = useSelector(state => state.User.balance);
    const {lastBet = 3} = useSelector(state => state.Modal.props);
    const [valueBet, setValueBet] = useState(lastBet);
    const dispatch = useDispatch();

    return (
        <div className="modal modal--bet" id="betModal">
            <div className="modal__top">
                <h6 className="modal__title">СДЕЛАТЬ СТАВКУ</h6>
                <a className="modal__close" data-izimodal-close><i className="ic-close"></i></a>
            </div>
            <div className="modal__body">
                <div className="conclusion__balance">
                    <p className="conclusion__balance-text">Текущий баланс:</p>
                    <p className="conclusion__balance-value">{balance} <span>coins</span></p>
                </div>
                <form className="bet__form">
                    <input type="hidden" name="balance" value="{{ getBalance(auth()->user()) }}"/>
                    <div className="bet__form-wrap">
                        <label className="bet__form-label">COINS</label>
                        <div id="coins" className="bet__form-box">
                            <input type="number"
                                // onChange="MainFunction.calculate(this, event, 'senyor');"
                                   onKeyUp="MainFunction.calculate(this, event, 'senyor');" step="2"
                                   className="bet__form-input cash-coins"
                                   value={valueBet}
                                   onChange={(e) => {
                                       let nextValue = Number(e.target.value);
                                       if (nextValue > lastBet) {
                                           if (balance >= nextValue) {
                                               setValueBet(nextValue);
                                           } else {
                                               setValueBet(balance);
                                           }
                                       } else {
                                           setValueBet(lastBet);
                                       }
                                   }}
                            />
                            <span className="bet__form-up"></span>
                            <span className="bet__form-down"></span>
                        </div>
                    </div>
                    <span className="bet__form-sum">=</span>
                    <div className="bet__form-wrap bet__form-wrap--gray">
                        <label className="bet__form-label">руб</label>
                        <div id="coins-result" className="bet__form-box count-money">
                            <input type="number" className="bet__form-input" readOnly value={valueBet / 10}/>
                            <span className="bet__form-up"></span>
                            <span className="bet__form-down"></span>
                        </div>
                    </div>
                </form>

                <div className="warning__balance hide">
                    <p className="warning__balance-text">* На вашем счете недостаточно средств для ставки</p>
                </div>
                <div className="modal__buttons">
                    <a href="javascript:void(0);" onClick="MainFunction.createGameKing('senyor');"
                       className="button button--gradient">СДЕЛАТЬ
                        СТАВКУ</a>
                    <a href="#" className="button" data-izimodal-open="#inputModal"
                       data-izimodal-transitionin="fadeInDown"
                       onClick={() => {
                           dispatch(closeModal);
                           setTimeout(()=>dispatch(openModal("InputPayment")),150)
                       }}
                    >Пополнить</a>
                </div>
            </div>
        </div>
    )
};

export {
    BetKingOfTheHill
}