import React from "react";


const JackpotStatistics = () => {
    return(
        <div className="games__box game">
            <h4 className="game__title">Jackpot</h4>
            <TableTitle/>
            <TableInner/>
        </div>
    )
};

const TableTitle = () => {
    return(
        <table className="game__table">
            <thead>
            <th className="game__table-col1">Название игры</th>
            <th className="game__table-col2">Hash</th>
            <th className="game__table-col3">Билет</th>
            <th className="game__table-col4">Банк</th>
            <th className="game__table-col5">Статус</th>
            <th className="game__table-col6">Победитель</th>
            </thead>
        </table>
    )
};


const TableInner = ({jackpotGames = [{},{},{},{},{},{},{}]}) => {
    return(
        <div className="nano game__scroll">
            <div className="nano-content">
                <table className="game__table">
                    <tbody>
                    {
                        jackpotGames.map(game => {
                            return <TableItem game={game}/>
                        })
                    }
                    </tbody>
                </table>
            </div>
        </div>
    )
};

const TableItem = ({game = {}}) => {
    const {
        gameName = "classic",
        gameID = 99,
        hash = "isakjpo21kokifpok1",
        winnerTicket = 9999,
        totalCash = 99999,
        gameStatus = 1,
        winnerName = "",
        winnerLastName = ""
    } = game;
    return(
        <tr>
            <td className="game__table-col1">{gameName} #{gameID}</td>
            <td className="game__table-col2">{hash}</td>
            <td className="game__table-col3">{winnerTicket}</td>
            <td className="game__table-col4">{totalCash}</td>
            <td className="game__table-col5">
                {
                    gameStatus === 1 ? "Создана" : gameStatus === 4 ? "В очереди" : gameStatus !== 4? "Запущена": "Закончена"
                }
            </td>
            {
                winnerName ?
                    <td className="game__table-col6">{winnerName} {winnerLastName}</td>
                        :
                    <td className="game__table-col6">Не выбран</td>

            }
        </tr>
    )
}

export {
    JackpotStatistics
}
