import React from "react";
const STATISTICS = ["Статистика пополнения","Статистика вывода","Статистика дохода","Количество новых пользователей","Статистика реф. начислений","Статистика по JACKPOT","Статистика по COINFLIP","Статистика по KING OF THE HILL"];

const TableStatistic = () => {
    return (
        <table className="statistics">
            <tbody>
            {STATISTICS.map(item => <TableStatisticItem item={{title:item}}/>)}
            </tbody>
        </table>
    )
};

const TableStatisticItem = ({item = {}}) => {
    const {title = "", inputToday = 0, inputYesterday = 0, input7days = 0, inputMonth = 0, input3month = 0} = item;
    return (
        <tr>
            <td className="statistics__title">{title}</td>
            <td>
                <span className="statistics__number">{inputToday}</span>
                <p className="statistics__period">За сегодня</p>
            </td>
            <td>
                <span className="statistics__number">{inputYesterday}</span>
                <p className="statistics__period">За вчера</p>
            </td>
            <td>
                <span className="statistics__number">{input7days}</span>
                <p className="statistics__period">За 7 дней</p>
            </td>
            <td>
                <span className="statistics__number">{inputMonth}</span>
                <p className="statistics__period">За месяц</p>
            </td>
            <td>
                <span className="statistics__number">{input3month}</span>
                <p className="statistics__period">За 3 месяца</p>
            </td>
        </tr>
    )
};

export {
    TableStatistic
}