import React from "react";
import {OutputTable} from "./UI/OutputTable";


function OutputMain() {
    return(
        <div className="base">
            <div className="base__wrapper base__wrapper--promo">
                <div className="table-base__checker checker">
                    <p className="checker__text">Выбрано <span className="check-counter">10</span> записей</p>
                    <a className="checker__all">Выбрать все записи<span></span></a>
                    <a className="checker__cancel">Отменить выделение<span></span></a>
                </div>
                <form action="{{ route(Route::currentRouteName())  }}" style={{display:"none"}} className="sorted-form">
                    <input type="text" name="sort"/>
                </form>
                <OutputTable/>
            </div>
        </div>
    )
}

export {
    OutputMain
}