import React from "react";
import { Switch, Route } from 'react-router-dom';
import HomePage from "./AdminPage/Home/HomePage";
import UsersPage from "./AdminPage/Users/UsersPage";
import WalletPage from "./AdminPage/Wallet/WalletPage";
import OutputPage from "./AdminPage/Output/OutputPage";
import PromocodesPage from "./AdminPage/Promocodes/PromocodesPage";
import GamesPage from "./AdminPage/Games/GamesPage";
import CrashPage from "./AdminPage/Crash/CrashPage";
import {Error404} from "./Pages/404/404";

function AdminApp (){
    return(
        <Switch>
            <Route exact path='/'>
                <HomePage/>
            </Route>
            <Route exact path='/crash'>
                <CrashPage/>
            </Route>
            <Route exact path='/games'>
                <GamesPage/>
            </Route>
            <Route exact path='/output'>
                <OutputPage/>
            </Route>
            <Route exact path='/Promocodes'>
                <PromocodesPage/>
            </Route>
            <Route exact path='/users'>
                <UsersPage/>
            </Route>
            <Route exact path='/wallet'>
                <WalletPage/>
            </Route>
            <Route component={Error404}/>
        </Switch>
    )
}
export {
    AdminApp
}