import React from "react";
import {Header} from "../block/Header";
import {SideMenuLeft} from "../../Components/Admin/SideMenuLeft";
import {PromocodesMain} from "./PromocodesMain";


class PromocodesPage extends React.Component {
    render() {
        return (
            <>
                <Header pageName={'Promocodes'}/>
                <div className="main">
                    <SideMenuLeft nowIs={'promocodes'}/>
                    <PromocodesMain/>
                </div>
            </>
        );
    }
}

export default PromocodesPage