import React from "react";
import {TableStatistic} from "./UI/Table";


function HomeMain() {
    return (
        <div className="base">
            <div className="base__wrapper">
                <div className="nano base__statistics">
                    <div className="nano-content">
                        <TableStatistic/>
                    </div>
                </div>
            </div>
        </div>
    )
}

export {
    HomeMain
}