import React from "react";



const PromocodesTable = () => {
    return(
        <>
            <PromocodesTableTitle/>
            <PromocodesTableInner/>
        </>
    )
};


const PromocodesTableTitle = () => {
    return(
        <table id="table1-top" className="table-base">
            <thead>
            <th className="table-base-col1">
                <label className="table-checkbox">
                    <input type="checkbox" className="table-checkbox__inp table-check--all"/>
                        <span className="table-checkbox__check--all"></span>
                </label>
            </th>
            <th className="table-base-col2 table-sort"
                data-order="{{ isset($sort) && $sort && isset($sort[0]) && $sort[0]->order == 'desc' ? 'desc' : 'asc' }}"
                data-column="id">ID<span></span></th>
            <th className="table-base-col3">Промокод</th>
            <th className="table-base-col4 table-sort"
                data-order="{{ isset($sort) && $sort && isset($sort[1]) && $sort[1]->order == 'desc' ? 'desc' : 'asc' }}"
                data-column="price">Сумма<span></span></th>
            </thead>
        </table>
    )
};

const PromocodesTableInner = ({promocodes = [{},{},{}]}) => {
    return (
        <div className="nano table__scroll">
            <div className="nano-content">
                <table id="table1-top" className="table-base">
                    <tbody>
                    {
                        promocodes.map(promocode => {
                            return <PromocodesTableItem promocode={promocode}/>
                        })
                    }
                    </tbody>
                </table>
            </div>
        </div>
    )
};


const PromocodesTableItem = ({promocode = {}}) => {
    const {id = 99, code = "12ljkjcfaisj1", price = 333} = promocode;
    return(
        <tr>
            <td className="table-base-col1">
                <label className="table-checkbox">
                    <input type="checkbox" className="table-checkbox__inp table-check--simple"/>
                        <span className="table-checkbox__check"></span>
                </label>
            </td>
            <td className="table-base-col2">{id}</td>
            <td className="table-base-col3">{code}</td>
            <td className="table-base-col4">{price}</td>
        </tr>
    )
}
export {
    PromocodesTable
}