import React from "react";
import {AdminApp} from "../AdminApp";
import {UserApp} from "../UserApp";
import {connect} from "react-redux";
import {setMainStyle} from "../Actions/UserAction";

const mapStateToProps = state => {
    return {
        role_id: state.User.role_id,
        mainStyleHref: state.User.mainStyleHref
    }
}
const mapDispatchToProps = dispatch => {
    return{
        setMainStyle: (href) => dispatch(setMainStyle(href))
    }
}


class AppWrapper extends React.Component{
    componentDidMount() {
        const role_id = this.props.role_id;
        let style = document.getElementById("mainStyle");
        if(role_id === 2){
            this.props.setMainStyle(style.href);
            style.href = "/foxx/static/css/admin.min.css"
        }
    }
    componentDidUpdate(prevProps, prevState, snapshot) {
        const role_id = this.props.role_id;
        let style = document.getElementById("mainStyle");
        if(role_id === 2){
            style.href = "/foxx/static/css/admin.min.css"
        } else {
            style.href = this.props.mainStyleHref
        }
    }

    render() {
        const role_id = this.props.role_id;
        return role_id === 2 ? <AdminApp/> : <UserApp/>
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(AppWrapper)