import React, {useState} from "react";
import {useDispatch} from "react-redux";
import {changeToRole, LIKE_ADMIN, LIKE_GUEST, LIKE_USER} from "../Actions/UserAction";

const ChangeAccount = () => {
    const [nowIs, setNowIs] = useState(0);
    const dispatch = useDispatch();
    return(
        <div>
            <label>
                Guest:
                <input type="checkbox" checked={nowIs === 0} value={0} onChange={()=>{
                    setNowIs(0);
                    dispatch(changeToRole(LIKE_GUEST))
                }}/>
            </label>
            <label>
                User:
                <input type="checkbox" checked={nowIs === 1} value={1} onChange={()=>{
                    setNowIs(1);
                    dispatch(changeToRole(LIKE_USER))
                }}/>
            </label>
            <label>
                Admin:
                <input type="checkbox" checked={nowIs === 2} value={2} onChange={()=>{
                    setNowIs(2);
                    dispatch(changeToRole(LIKE_ADMIN))

                }}/>
            </label>
        </div>
    )
};

export {
    ChangeAccount
}