import React from "react";


const CoinFlipStatistics = () => {
    return (
        <div className="games__box game game--last">
            <h4 className="game__title">Coin Flip</h4>
            <TableTitle/>
            <TableInner/>
        </div>
    )
};

const TableTitle = () => {
    return (
        <table className="game__table game__table--seven">
            <thead>
            <th className="game__table-col1">Название игры</th>
            <th className="game__table-col2">Hash</th>
            <th className="game__table-col3">Билет</th>
            <th className="game__table-col4">Банк</th>
            <th className="game__table-col5">Статус</th>
            <th className="game__table-col6">Победитель</th>
            <th className="game__table-col7">Цвет победителя</th>
            </thead>
        </table>
    )
};

const TableInner = ({games = [{},{},{}]}) => {
    return (
        <div className="nano game__scroll">
            <div className="nano-content">
                <table className="game__table game__table--seven">
                    <tbody>
                    {
                        games.map(game => {
                            return <TableItem game={game}/>
                        })
                    }
                    </tbody>
                </table>
            </div>
        </div>
    )
};

const TableItem = ({game}) => {
    const {
        gameID = 999,
        hash = "jiofjkp13jpo9ju9o",
        winnerTicket = 9999,
        totalCash = 99999,
        countOfPlayer = 1,
        createAccountId = "",
        winnerName = "nameless",
        participant = ""

    } = game;
    return(
        <tr>
            <td className="game__table-col1">{gameID}</td>
            <td className="game__table-col2">{hash}</td>
            <td className="game__table-col3">{winnerTicket}</td>
            <td className="game__table-col4">{totalCash}</td>
            <td className="game__table-col5">
                {
                    countOfPlayer === 1? "Создана" : createAccountId ? "В очереди" : "Законченна"
                }
            </td>
            {
                winnerName ?
                    <td className="game__table-col6">{winnerName}</td>
                    :
                    participant ?
                        <td className="game__table-col6">{participant}</td>
                        :
                        <td className="game__table-col6">Неизвестно</td>
            }
            {
                winnerTicket ?
                    winnerTicket <=50?
                        <td className="game__table-col7"><span className="game__color game__color--yellow"></span></td>
                        :
                        <td className="game__table-col7"><span className="game__color game__color--blue"></span></td>
                    :
                    <td className="game__table-col7">Неизвестно</td>
            }
        </tr>
    )
}


export {
    CoinFlipStatistics
}