import React from "react";
import {CrashResultTable} from "./CrashTableBet";


const CrashResult = () => {
    return(
        <div className="crash-game" style={{display: "flex !important", flexWrap: "wrap"}}>
            <CrashResultLeft/>
            <CrashResultRight/>
        </div>
    )
};


const CrashResultLeft = () => {
    return(
        <div className="left">
            <small style={{color: "brown"}}>
                {/*{{print_r(date('H:i:s', $info->stop_game),1)}}*/}
                <br/>
                    {/*{{print_r(date('H:i:s', time()),1)}}*/}
                    <br/>
                        <br/>
                            <b>stop_game:</b>
                            {/*{{print_r($info->stop_game,1)}}*/}
                            <br/>
                            <b>mode:</b>
                            {/*{{print_r($mode,1)}}*/}
                            <br/>
                            <b>status:</b>
                            {/*{{print_r($info->status,1)}}*/}
                            <br/>
                            <b>rand_number:</b>
                            {/*{{print_r($info->rand_number,1)}}*/}
                            <br/>
                            <b>profit:</b>
                            {/*{{print_r($info->profit,1)}}*/}
                            <br/>
                            <b>updated_at:</b>
                            {/*{{print_r($info->updated_at,1)}}*/}
                            <br/>
            </small>
            <div className="info">
                Игра №<b>{/*{$info->id}*/}</b>
            </div>
            <div className="total">
                <span id="total_x" title="Текущий коэфициент">
                    {/*{{number_format((float)$rand_number, 2, '.', '')}}*/}22
                </span>X
                <small id="total_x" style={{color: "#1f92bf"}} title="Профит игры">
                    {/*@if($info->profit)*/}
                    {/*#1f92bf*/}
                    {/*@else*/}
                    {/*red*/}
                    {/*@endif*/}
                    {/*{{number_format((float)$info->profit, 2, '.', '')}}*/}
                    22X
                </small>
            </div>
            <div className="info">Онлайн в Crash: 999</div>
            <button onClick="stop_game();" className="btn btn-stop-game">Остановить график?</button>
        </div>
    )
};
const CrashResultRight = () => {
    return(
        <div className="right">
            <div className="table-bets">
                <CrashResultTable/>
            </div>
        </div>
    )
};


export {
    CrashResult
}