import defImg from "../assets/img/fox.png";
import {CHANGE_SESSION_THEME, LIKE_ADMIN, LIKE_GUEST, LIKE_USER, SET_MAIN_STYLE} from "../Actions/UserAction";

const UserReducer = (state = {
    isAuth: false,
    sessionTheme: true,
    role_id: 0,
    balance: 3001,
    countOfCoinflipGame: 30,
    avatar: defImg,
    lvl: 4,
    id: 0,
    mainStyleHref : "",
}, action) => {
    switch (action.type) {

        //test action handle for role
        case LIKE_GUEST: return {
            ...state,
            isAuth: false,
        };
        case LIKE_USER: return {
            ...state,
            isAuth: true,
            role_id: 0
        };
        case LIKE_ADMIN: return {
            ...state,
            isAuth: true,
            role_id: 2
        };
        case SET_MAIN_STYLE: return {
            ...state,
            mainStyleHref: action.href
        };
        //end
        case CHANGE_SESSION_THEME: return {
            ...state,
            sessionTheme: !state.sessionTheme
        }
        default: return state
    }
};


export {
    UserReducer
}