import React from 'react';
import { Switch, Route } from 'react-router-dom';
import './App.css';
import "./assets/css/admin/admin.fontFace.css";
import "./assets/css/main.min.css";
import {useDispatch, useSelector} from "react-redux";
import "../node_modules/izimodal/css/iziModal.min.css";
import Modal from "react-awesome-modal";
import {closeModal} from "./Actions/ModalAction";
import "./assets/css/PopupStyle.css"
import {ChangeAccount} from "./test/ChangeAccount";
import {AdminApp} from "./AdminApp";
import {UserApp} from "./UserApp";
import {MODALS} from "./Help/Modals";
import AppWrapper from "./Wrapper/AppWrapper";


function App() {
    const isVisible = useSelector(state => state.Modal.isVisible);
    const nowIs = useSelector(state => state.Modal.nowIs);
    const role_id = useSelector(state => state.User.role_id);
    const dispatch = useDispatch();
    const width = window.innerWidth;
    return (
        <div className="App">
            <ChangeAccount/>
            <AppWrapper/>
            <Modal visible={isVisible} className="lol" width={width < 500 ? "100%" : "65%"} onClickAway={() => dispatch(closeModal)}>
                {MODALS[nowIs]}
            </Modal>
        </div>
    );
}


export default App;
